@import url('https://fonts.googleapis.com/css?family=Roboto+Mono:400,500&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-family: 'Roboto Mono', monospace;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body * {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.close{
  position: absolute;
  right: 20px;
  top: 10px;
  font-size: 20px;
  font-weight: 600;
  color: #707070;
  cursor: pointer;
}
.fixedBg{
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.1);
}
.userArea{
  display: flex;
  flex-direction: column;
}

.loginbtn{
  position: absolute;
  top: 10px;
  right: 10px;
  border: 1.2px solid rgba(82,102,132);
  border-radius: 3px;
  padding: 5px;
  color: rgba(82,102,132);
}

.unloginBtn:hover{
  cursor: pointer;
  background-color: rgba(82,102,132,0.3)
}

.loginUser{
  background-color: rgba(82,102,132);
  color: #ffffff;
  border-radius: 5px;
  padding: 5px 10px;
  border: none;
  font-weight: 500;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16);
}
.loginUser{
  color: #ffffff;
}
.loginUser span:first-child{
  font-weight: 600;
  font-size: 20px;
}

.logoutbtn:hover{
  font-weight: 600;
  cursor: pointer;
}

.loginForm{
  padding: 40px 30px;
  width: 320px;
  height: 300px;
  background-color: rgba(255, 255, 255, 1);
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -160px 0 0 -125px; 
  border-radius: 10px;
  box-shadow: 0 15px 30px rgba(82,102,132,0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.loginForm form{
  width: 100%;
  text-align: center;
}
.loginForm .myInput{
  border: 1px solid #707070;
  border-radius: 3px;
  height: 30px;
  margin-bottom: 10px;
  width: 80%;
  padding: 3px 10px;
}
.loginForm > div:first-child{
  font-size: 22px;
  text-align: center;
  color: rgba(82,102,132);
  font-weight: 600;
}
.loginForm input[type='submit']{
  background-color: rgba(82,102,132);
  border: none;
  color: #ffffff;
  cursor: pointer;
}
.loginForm input[type='submit']:hover{
  background-color: rgba(82,102,132,0.8);
  border: 1px solid rgba(82,102,132);
  cursor: pointer;
}
.loginForm > div:nth-last-child(2){
  font-size: 14px;
  text-align: center;
}
.loginForm > div:nth-last-child(2) > span{
  cursor: pointer;
  margin-right: 5px;
  color: rgb(41,126,129);
}
.loginForm > div:last-child > span:hover{
  text-decoration: underline;
}

.loginForm .errormessage{
  background-color: rgba(254,221,225);
  padding: 3px;
  margin-bottom: 6px;
}

.loginForm .warningmessage{
  background-color: rgba(252,247,220);
  padding: 0 3px;
  margin-top: 6px;
}
.loginForm .formTitle{
  margin-bottom: 10px;
}

.tasks{
  background-color: #F7F3EC;
  height: 510px;
  width: 260px;
}
.App{
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  background: linear-gradient(90deg, rgba(245,246,248,1) 0%, rgba(239,244,248,1) 100%);
  ;
}

.bookmarkBtn{
  width: 74px;
  height: 62px;
  border-radius: 20px;
  background-color: rgba(82,102,132);
  position: absolute;
  left: -20px;
  display: flex;
  align-items: center;
  padding-left: 18px;
}

.bookmarkBtnKey{
  top: 60px;
  z-index: 10;
}

.bookmarkBtnMonth{
  top: 130px;
  z-index: 10;
}

.bookmarkBtn:hover{
  cursor: pointer;
  background-color: rgba(82,102,132,0.6);
}

.bookmarkBtn img{
  margin-right: auto;
  display: block;
}
.keyBookmark{
  position: absolute;
  background-color: rgba(82,102,132,0.7);
  top: 50px;
  left: -20px;
  border-radius: 20px;
  width: 220px;
  padding: 10px 10px 10px 90px;
  color: white;
  font-weight: 500; 
  font-size: 18px;
  z-index: 2;
}
.keyBookmark > ul > li > span:first-child{
  margin-right: 10px;
}

.monthBookmarkEmpty{
  width: 0;
}
.monthBookmark{
  margin-right: 20px;
  height: 100%;
  width: 360px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 60px 0px;
  color: rgba(82,102,132);
  border-right: 1px solid #eff4f8;
  overflow-y: scroll;
}
.monthBookmark > div{
  width: 66%;
  margin-left: 70px;
  margin-top: 10px;
}

.monthBookmark > div:first-child > div:first-child{
  border-bottom: none;
}

.monthList{
  /* padding: 5px; */
  height: 100px;
  background-color: #ffffff;
  overflow: scroll;
  display: inline-block;
  position: absolute;
  right: 0;
  z-index: 100;
  box-shadow: 0px 3px 6px rgba(0,0,0,0.16);
}

.monthItem{
  margin: 1px 5px;
  display: inline-block;
  font-weight: 500;
}

.monthItem:hover{
  cursor: pointer;
  background-color: rgba(82,102,132 0.3);
}

.calenderTitle > span > div:first-child {
  font-weight: 600;
}
.calenderTitle > span{
  margin-right: 10px;
}

.calender > div:first-child{
  border-bottom: 2px solid rgba(82,102,132);
  font-weight: 600;
}

.calender > div{
  display: flex;
  justify-content: space-between;
  margin-top: 2px;
  height: 25px;
}

.calender > div > div{
  display: inline-block;
  width: 22px;
  text-align: right;
  height: 22px;
}

.calender > div > .dateInCalender:hover{
  cursor: pointer;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  font-weight: 500;
  background-color: rgba(82,102,132,0.3);
}

.calender .chooseDate{
  background-color: rgba(82,102,132);
  color: white;
  height: 25px;
  width: 26px;
  position: relative;
  left: 3px;
  text-align: center;
  border-radius: 50%;
  font-weight: 500;
}

.listOfTask{
  position: relative;
}

.bulletType{
  width: 23px;
  height: 23px;
  text-align: center;
  font-weight: 600;
}
.bulletType:hover{
  border-radius: 5px;
  background-color: rgba(255, 255, 255);
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.9);
  color: #000;
  cursor: pointer;
}

.relative{
  position: relative;
}

li > span {
  display: inline-block;
}

.bulletList{
  position: absolute;
  background-color: #ffffff;
  display: flex;
  z-index: 10;
  box-shadow: 0;
  box-shadow: 0px 3px 6px rgba(0,0,0,0.16);
  padding: 2px;
}
.bulletList > div {
  width: 23px;
  height: 23px;
  /* padding: 2px; */
  border-radius: 5px;
  text-align: center;
  line-height: 22px;
  /* height: 30px; */
}
.bulletList > div:hover {
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0,0.5);
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.9);
}
.taskEditLi{
  position: relative;
  background-color: #ffffff;
}
ul{
  padding: 0;
  margin: 0;
  list-style-type: none;
}

li{
  display: flex;
  margin-bottom: 5px;
}


.myInput{
  height: auto;
  color: #707070;
  border: none;
  width: 100%;
  padding: 3px 0px 3px 5px;
  font-size: 16px;
  font-family: 'Roboto Mono', monospace;
  border-radius: 6px;
  background-color: rgba(82,102,132,0.1);
}

.flex{
  display: flex;
}

.delete{
  color: #DEDAD4;
  position: absolute;
  right: 5px;
  margin: auto 0;
  z-index: 10;
}
.delete:hover{
  cursor: pointer;
}

.mainArea{
  width: 80vw;
  padding-left: 20px;
  overflow-x: scroll;

}

.center{
  align-items: center;
}

.test{
  position: absolute;
  top: -27px;
  left: 0px;
}

.dateTasksDiv{
  position: relative;
  padding: 20px 20px;
  width: 260px;
  background-color: #ffffff;
  height: 550px;
  border-right: 1px solid #eff4f8;
  float: left;
}

.tasksStyle > ul{
  margin-top: 5px;
  margin-left: 5px
}

.tasksList {
  color: #707070;
}

.tasksList span:first-child{
  margin-right: 5px;
}
.tasksList li > span:nth-child(2){
  width: 100%;
}

.lineThrough{
  text-decoration: line-through;
}
.monthSquare{
  display: inline-block;
  background-color: rgba(82,102,132);;
  color: white;
  padding: 3px 5px;
  font-weight: 600;
}

.monthTasks{
  margin-top: 80px;
}

.chooseDay{
  background-color: #ffffff;
}

.chooseDay > div > div:first-child{
  font-weight: 600;
  background-color:rgba(82,102,132, 0.3);
  line-height: 22px;
  padding-top: 4px;
  padding-bottom: 0px;
}

.tasksStyle > div:first-child{
  border-bottom: 2px solid rgba(82,102,132);
  padding-bottom: 5px;
  text-align: center;
  width: 100%;
  font-weight: 500;
  color: rgba(82,102,132);;
}
